import { Component, OnInit } from '@angular/core';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { PartnerFilter } from '../partner-filter';
import { PartnerService } from '../partner.service';
import { Partner } from '../partner';

@Component({
    selector: 'partner',
    templateUrl: 'partner-list.component.html'
})
export class PartnerListComponent {

    filter = new PartnerFilter();
    selectedPartner: Partner;
    errors = '';

    get partnerList(): Partner[] {
        return this.partnerService.partnerList;
    }

    constructor(private partnerService: PartnerService) {
    }

    ngOnInit() {
        this.search();
    }

    search(): void {
        this.partnerService.load(this.filter);
    }

    select(selected: Partner): void {
        this.selectedPartner = selected;
    }

    delete(partner: Partner): void {
        if (confirm("Are you sure?")) {
            this.partnerService.delete(partner).subscribe(
                partner => {
                    this.errors = 'Delete was successful!';
                    setTimeout(() => {
                        this.search();
                    },1000);                   
                },
                err => {
                    this.errors = err;
                }
            );
        }        
        
    }    

}
