import { Rateplan } from './rateplan';
import { RateplanFilter } from './rateplan-filter';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';

@Injectable()
export class RateplanService {
    
    baseUrl = `/rateplans`;
    constructor(private http: HttpClient) {
    }

    rateplanList: Rateplan[] = [];
  
    findById(id: string): Observable<Rateplan> {
        let url = `${this.baseUrl}/${id}`; 
        let params = { "id": id };
        let headers = new HttpHeaders()
                            .set('Accept', 'application/json');
        return this.http.get<Rateplan>(url, {params, headers});
    }
    
    load(filter: RateplanFilter): void {
        this.find(filter).subscribe(
            result => {
                this.rateplanList = result;
            },
            err => {
                console.error('error loading', err);
            }
        )
    }

    find(filter: RateplanFilter): Observable<Rateplan[]> {
        let url = `${this.baseUrl}`;
        let headers = new HttpHeaders()
                            .set('Accept', 'application/json');

        let params = {
            "ratePlanTitle": filter.ratePlanTitle,
        };

        return this.http.get<Rateplan[]>(url, {params, headers});
    }

    save(entity: Rateplan): Observable<Rateplan> {        
        var params = new HttpParams();
        var url = "";
        const headers = new HttpHeaders().set('content-type', 'application/json');  
        if(entity.id) {
            url = `${this.baseUrl}/${entity.id.toString()}`;
            params = new HttpParams().set('ID', entity.id.toString());  
            return this.http.put<Rateplan>(url, entity, {headers, params});
        }
        else {
            url = `${this.baseUrl}`;
            return this.http.post<Rateplan>(url, entity, {headers, params});
        }        
    }


    delete(entity: Rateplan): Observable<Rateplan> {
        var params = new HttpParams();
        var url = "";
        const headers = new HttpHeaders().set('content-type', 'application/json');  
        if(entity.id) {
            url = `${this.baseUrl}/${entity.id.toString()}`;
            params = new HttpParams().set('ID', entity.id.toString());  
            return this.http.delete<Rateplan>(url, {headers, params});
        }

        return null;
    }       
}

