import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Router } from '@angular/router';
import { PriceplanService } from '../priceplan.service';
import { Priceplan } from '../priceplan';
import { map, switchMap } from 'rxjs/operators';
import { of } from 'rxjs';

@Component({
  selector: 'app-priceplan-edit',
  templateUrl: './priceplan-edit.component.html'
})
export class PriceplanEditComponent implements OnInit {

  id: string;
  priceplan: Priceplan;
  feedback: any = {};

  constructor(
    private route: ActivatedRoute,
    private router: Router,
    private priceplanService: PriceplanService) {
  }

  ngOnInit() {
    this
      .route
      .params
      .pipe(
        map(p => p.id),
        switchMap(id => {
          if (id === 'new') { return of(new Priceplan()); }
          return this.priceplanService.findById(id);
        })
      )
      .subscribe(priceplan => {
        //priceplan.effectiveFrom = new Date(priceplan.effectiveFrom).toLocaleDateString();
       // let formatted_date = (priceplan.effectiveFrom.getMonth() + 1) + "/" + (priceplan.effectiveFrom.getDay()) + "/" + priceplan.effectiveFrom.getFullYear()
       // console.log(formatted_date)
          this.priceplan = priceplan;
          this.feedback = {};
        },
        err => {
          this.feedback = {type: 'warning', message: 'Error loading'};
        }
      );
  }

  save() {
    this.priceplanService.save(this.priceplan).subscribe(
      priceplan => {
        this.priceplan = priceplan;
        this.feedback = {type: 'success', message: 'Save was successful!'};
        setTimeout(() => {
          this.router.navigate(['/home/priceplans']);
        }, 1000);
      },
      err => {
        this.feedback = {type: 'warning', message: 'Error saving'};
      }
    );
  }

  cancel() {
    this.router.navigate(['/home/priceplans']);
  }

  applyPricePlan() {
    this.priceplanService.applyPricePlan(this.priceplan).subscribe(
      priceplan => {
        this.priceplan.isApplied = true;
        this.feedback = {type: 'success', message: 'Save was successful!'};
        // setTimeout(() => {
        //   this.router.navigate(['/home/priceplans']);
        // }, 1000);
      },
      err => {
        this.feedback = {type: 'warning', message: 'Error saving'};
      }
    );
  }

  stopPricePlan() {
    this.priceplanService.stopPricePlan(this.priceplan).subscribe(
      priceplan => {
        this.priceplan.isApplied = false;
        this.feedback = {type: 'success', message: 'Save was successful!'};
        // setTimeout(() => {
        //   this.router.navigate(['/home/priceplans']);
        // }, 1000);
      },
      err => {
        this.feedback = {type: 'warning', message: 'Error saving'};
      }
    );
  }


}
