import { Partner } from './partner';
import { PartnerFilter } from './partner-filter';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';

@Injectable()
export class PartnerService {
    
    baseUrl = `/partners`;
    constructor(private http: HttpClient) {
    }

    partnerList: Partner[] = [];
  
    findById(id: string): Observable<Partner> {
        let url = `${this.baseUrl}/${id}`; 
        let params = { "id": id };
        let headers = new HttpHeaders()
                            .set('Accept', 'application/json');
        return this.http.get<Partner>(url, {params, headers});
    }
    
    load(filter: PartnerFilter): void {
        this.find(filter).subscribe(
            result => {
                this.partnerList = result;
            },
            err => {
                console.error('error loading', err);
            }
        )
    }

    find(filter: PartnerFilter): Observable<Partner[]> {
        let url = `${this.baseUrl}`;
        let headers = new HttpHeaders()
                            .set('Accept', 'application/json');

        let params = {
            "phone": filter.phone,
        };

        return this.http.get<Partner[]>(url, {params, headers});
    }

    save(entity: Partner): Observable<Partner> {        
        var params = new HttpParams();
        var url = "";
        const headers = new HttpHeaders().set('content-type', 'application/json');  
        if(entity.id) {
            url = `${this.baseUrl}/${entity.id.toString()}`;
            params = new HttpParams().set('ID', entity.id.toString());  
            return this.http.put<Partner>(url, entity, {headers, params});
        }
        else {
            url = `${this.baseUrl}/authenticate`;
            return this.http.post<Partner>(url, entity, {headers, params});
        }        
    }


    delete(entity: Partner): Observable<Partner> {
        var params = new HttpParams();
        var url = "";
        const headers = new HttpHeaders().set('content-type', 'application/json');  
        if(entity.id) {
            url = `${this.baseUrl}/${entity.id.toString()}`;
            params = new HttpParams().set('ID', entity.id.toString());  
            return this.http.delete<Partner>(url, {headers, params});
        }

        return null;
    }       
}

