import { Dashboard } from './dashboard';
import { DashboardFilter } from './dashboard-filter';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
//import {HttpHeaderService} from '../services/index'

@Injectable()
export class DashboardService {
    
    baseUrl = `/dashboards`;
    constructor(private http: HttpClient) {
    }

    dashboardList: Dashboard[] = [];
  
    findById(id: string): Observable<Dashboard> {
        
        let url = `${this.baseUrl}/${id}`; 
        let params = { "id": id };
         let headers = new HttpHeaders()
                             .set('Accept', 'application/json')
                                    
        return this.http.get<Dashboard>(url, {params, headers});
    }
    
    load(filter: DashboardFilter): void {
        
        this.find(filter).subscribe(
            result => {
                this.dashboardList = result;
            },
            err => {
                console.error('error loading', err);
            }
        )
    }

    find(filter: DashboardFilter): Observable<Dashboard[]> {
        let url = `${this.baseUrl}/stats`;      
        let headers = new HttpHeaders()
                            .set('Accept', 'application/json');

        let params = {
          //  "phone": filter.phone,
        };

        return this.http.get<Dashboard[]>(url, {params, headers});
    }
    
}

