import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Router } from '@angular/router';
import { ConfigurationService } from '../configuration.service';
import { Configuration } from '../configuration';
import { map, switchMap } from 'rxjs/operators';
import { of } from 'rxjs';

@Component({
  selector: 'app-configuration-edit',
  templateUrl: './configuration-edit.component.html'
})
export class ConfigurationEditComponent implements OnInit {

  id: string;
  configuration: Configuration;
  feedback: any = {};

  constructor(
    private route: ActivatedRoute,
    private router: Router,
    private configurationService: ConfigurationService) {
  }

  ngOnInit() {
    this
      .route
      .params
      .pipe(
        map(p => p.id),
        switchMap(id => {
          if (id === 'new') { return of(new Configuration()); }
          return this.configurationService.findById(id);
        })
      )
      .subscribe(configuration => {
          this.configuration = configuration;
          this.feedback = {};
        },
        err => {
          this.feedback = {type: 'warning', message: 'Error loading'};
        }
      );
  }

  save() {
    this.configurationService.save(this.configuration).subscribe(
      configuration => {
        this.configuration = configuration;
        this.feedback = {type: 'success', message: 'Save was successful!'};
        setTimeout(() => {
          this.router.navigate(['/home/configurations']);
        }, 1000);
      },
      err => {
        this.feedback = {type: 'warning', message: 'Error saving'};
      }
    );
  }

  cancel() {
    this.router.navigate(['/home/configurations']);
  }
}
