import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Router } from '@angular/router';
import { SupplyService } from '../supply.service';
import { Supply } from '../supply';
import { PartnerService } from '../../partner/partner.service';
import { Partner } from '../../partner/partner';
import { PartnerFilter } from '../../partner/partner-filter';
import { ProductService } from '../../product/product.service';
import { Product } from '../../product/product';
import { ProductFilter } from '../../product/product-filter';
import { map, switchMap } from 'rxjs/operators';
import { of } from 'rxjs';
import { FileDetector } from 'selenium-webdriver/remote';

@Component({
  selector: 'supply-edit',
  templateUrl: './supply-edit.component.html'
})
export class SupplyEditComponent implements OnInit {

    id: string;
    supply: Supply;
    errors: string;
    buyingPriceUnitId_list: any;
    quentityUnitsId_list: any;

    partnerFilter = new PartnerFilter();
    
 
    get supplierId_list(): Partner[] {
        return this.partnerService.partnerList;
    }
    
    productFilter = new ProductFilter();
 
    get productsId_list(): Product[] {
        return this.productService.productList;
    }


    constructor(
        private route: ActivatedRoute,
        private router: Router,
        private supplyService: SupplyService,
        private partnerService: PartnerService,
        private productService: ProductService,
        ) { 
    }

    ngOnInit() {
        this
            .route
            .params
            .pipe(
                map(p => p['id']),
                switchMap(id => {
                    if (id === 'new') return of(new Supply());
                    return this.supplyService.findById(id)
                })
            )
            .subscribe(
                supply => { 
                    this.supply = supply; 
                    this.errors = ''; 
                },
                err => { 
                    this.errors = 'Error loading'; 
                }
            );

            this.buyingPriceUnitId_list = [{id:1, unitDisplayName: "kg" },{id:2, unitDisplayName: "dozen" },{id:3, unitDisplayName: "250 GM" },{id:4, unitDisplayName: "Bundle" } ];
            this.quentityUnitsId_list = [{id:1, unitDisplayName: "kg" },{id:2, unitDisplayName: "dozen" },{id:3, unitDisplayName: "250 GM" },{id:4, unitDisplayName: "Bundle" } ];
            this.partnerService.load(this.partnerFilter);
            this.productService.load(this.productFilter);
    }

    save() {
        this.supplyService.save(this.supply).subscribe(
            supply => { 
                this.supply = supply; 
                this.errors = 'Save was successful!'; 
                setTimeout(() => {
                    this.router.navigate(['/home/supply']);
                }, 1000);                   
            },
            err => { 
                this.errors = 'Error saving'; 
            }
        );
    }

    cancel() {
        this.router.navigate(['/home/supply']);
    } 
    
    selectedProduct($event) {
        let seletectProduct = this.productsId_list.filter( p => p.id == this.supply.productsId);
        console.log(seletectProduct);
        if(seletectProduct.length > 0){
            this.supply.quentityOnHand = seletectProduct[0].quentityOnHand;
            this.supply.productTitle = seletectProduct[0].displayName;
        }
    }
    
    selectedSupplier($event) {
        let selectedSupplier = this.supplierId_list.filter( s => s.id == this.supply.supplierId);
        console.log(selectedSupplier);
        if(selectedSupplier.length > 0){
            this.supply.supplierName = selectedSupplier[0].name;
            
        }
    }
    
    supplyArrived(supplyId : string) {
        
        this.supplyService.supplyArrived(supplyId)
        .subscribe(
            sp=>{
                this.supply.isArrived = sp.isArrived;
                this.errors = "Supply Arrived Successfully!"
            },
            err=>{
                this.errors = JSON.stringify(err)
            }
        );
    }


}