import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Router } from '@angular/router';
import { RateplanService } from '../rateplan.service';
import { Rateplan } from '../rateplan';
import { map, switchMap } from 'rxjs/operators';
import { of } from 'rxjs';
import { FileDetector } from 'selenium-webdriver/remote';

@Component({
  selector: 'rateplan-edit',
  templateUrl: './rateplan-edit.component.html'
})
export class RateplanEditComponent implements OnInit {

    id: string;
    rateplan: Rateplan;
    errors: string;
    
    

    constructor(
        private route: ActivatedRoute,
        private router: Router,
        private rateplanService: RateplanService) { 
    }

    ngOnInit() {
        this
            .route
            .params
            .pipe(
                map(p => p['id']),
                switchMap(id => {
                    if (id === 'new') return of(new Rateplan());
                    return this.rateplanService.findById(id)
                })
            )
            .subscribe(
                rateplan => { 
                    this.rateplan = rateplan; 
                    this.errors = ''; 
                },
                err => { 
                    this.errors = err; 
                }
            );
    }

    save() {
        this.rateplanService.save(this.rateplan).subscribe(
            rateplan => { 
                this.rateplan = rateplan; 
                this.errors = 'Save was successful!'; 
                setTimeout(() => {
                    this.router.navigate(['/rateplan']);
                }, 1000);                   
            },
            err => { 
                this.errors = 'Error saving'; 
            }
        );
    }

    cancel() {
        this.router.navigate(['/rateplan']);
    }    
}