import { Configuration } from './configuration';
import { ConfigurationFilter } from './configuration-filter';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';

const headers = new HttpHeaders().set('Accept', 'application/json');

@Injectable()
export class ConfigurationService {
  configurationList: Configuration[] = [];
  api = '/configurations';

  constructor(private http: HttpClient) {
  }

  findById(id: string): Observable<Configuration> {
    const url = `${this.api}/${id}`;
    const params = { id: id };
    return this.http.get<Configuration>(url, {params, headers});
  }

  load(filter: ConfigurationFilter): void {
    this.find(filter).subscribe(result => {
        this.configurationList = result;
      },
      err => {
        console.error('error loading', err);
      }
    );
  }

  find(filter: ConfigurationFilter): Observable<Configuration[]> {
    const params = {
    };

    return this.http.get<Configuration[]>(this.api, {params, headers});
  }

  save(entity: Configuration): Observable<Configuration> {
    let params = new HttpParams();
    let url = '';
    if (entity.id) {
      url = `${this.api}/${entity.id.toString()}`;
      params = new HttpParams().set('ID', entity.id.toString());
      return this.http.put<Configuration>(url, entity, {headers, params});
    } else {
      url = `${this.api}`;
      return this.http.post<Configuration>(url, entity, {headers, params});
    }
  }

  delete(entity: Configuration): Observable<Configuration> {
    let params = new HttpParams();
    let url = '';
    if (entity.id) {
      url = `${this.api}/${entity.id.toString()}`;
      params = new HttpParams().set('ID', entity.id.toString());
      return this.http.delete<Configuration>(url, {headers, params});
    }
    return null;
  }
}

