import { Priceplan } from './priceplan';
import { PriceplanFilter } from './priceplan-filter';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';

const headers = new HttpHeaders().set('Accept', 'application/json');

@Injectable()
export class PriceplanService {
  priceplanList: Priceplan[] = [];
  api = '/price-plans';

  constructor(private http: HttpClient) {
  }

  findById(id: string): Observable<Priceplan> {
    const url = `${this.api}/${id}`;
    const params = { id: id };
    return this.http.get<Priceplan>(url, {params, headers});

  }

  load(filter: PriceplanFilter): void {
    this.find(filter).subscribe(result => {
        this.priceplanList = result;
      },
      err => {
        console.error('error loading', err);
      }
    );
  }

  find(filter: PriceplanFilter): Observable<Priceplan[]> {
    const params = {
      'title': filter.title,
    };

    return this.http.get<Priceplan[]>(this.api, {params, headers});
  }

  save(entity: Priceplan): Observable<Priceplan> {
    let params = new HttpParams();
    let url = '';
    entity.effectiveFrom = new Date(entity.effectiveFrom);
    entity.effectiveTo = new Date(entity.effectiveTo);

    if (entity.id) {
      url = `${this.api}/${entity.id.toString()}`;
      params = new HttpParams().set('ID', entity.id.toString());
      return this.http.put<Priceplan>(url, entity, {headers, params});
    } else {
      url = `${this.api}`;
      return this.http.post<Priceplan>(url, entity, {headers, params});
    }
  }

  delete(entity: Priceplan): Observable<Priceplan> {
    let params = new HttpParams();
    let url = '';
    if (entity.id) {
      url = `${this.api}/${entity.id.toString()}`;
      params = new HttpParams().set('ID', entity.id.toString());
      return this.http.delete<Priceplan>(url, {headers, params});
    }
    return null;
  }

  applyPricePlan(entity: Priceplan): Observable<Priceplan> {
    let params = new HttpParams();
    let url = '';
      url = `${this.api}/${entity.id.toString()}/apply`;
      params = new HttpParams().set('ID', entity.id.toString());
      return this.http.put<Priceplan>(url, {}, {headers, params});
  
  }

  stopPricePlan(entity: Priceplan): Observable<Priceplan> {
    let params = new HttpParams();
    let url = '';
      url = `${this.api}/${entity.id.toString()}/stop`;
      params = new HttpParams().set('ID', entity.id.toString());
      return this.http.put<Priceplan>(url, {}, {headers, params});
  
  }


}

