import { Component, OnInit } from '@angular/core';
import { OrdersFilter } from '../orders-filter';
import { OrdersService } from '../orders.service';
import { Orders } from '../orders';

@Component({
  selector: 'app-orders',
  templateUrl: 'orders-list.component.html'
})
export class OrdersListComponent implements OnInit {

  filter = new OrdersFilter();
  selectedOrders: Orders;
  feedback: any = {};

  get ordersList(): Orders[] {
    return this.ordersService.ordersList;
  }

  constructor(private ordersService: OrdersService) {
  }

  ngOnInit() {
//    this.search();
  }

  search(): void {
    this.ordersService.load(this.filter);
  }

  select(selected: Orders): void {
    this.selectedOrders = selected;
  }

  delete(orders: Orders): void {
    if (confirm('Are you sure?')) {
      this.ordersService.delete(orders).subscribe(() => {
          this.feedback = {type: 'success', message: 'Delete was successful!'};
          orders.isDeleted = true;
          orders.orderStatus = 'Deleted';
        },
        err => {
          this.feedback = {type: 'warning', message: 'Error deleting.'};
        }
      );
    }
  }
}
