import { Component, OnInit } from '@angular/core';
import { OrderratingFilter } from '../orderrating-filter';
import { OrderratingService } from '../orderrating.service';
import { Orderrating } from '../orderrating';

@Component({
  selector: 'app-orderrating',
  templateUrl: 'orderrating-list.component.html'
})
export class OrderratingListComponent implements OnInit {

  filter = new OrderratingFilter();
  selectedOrderrating: Orderrating;
  feedback: any = {};
  averageUserRating: any ;

  get orderratingList(): Orderrating[] {
    return this.orderratingService.orderratingList;
   // return this.orderratingService.ratingCalculatedList;
  }

  constructor(private orderratingService: OrderratingService) {
  }

  ngOnInit() {
    this.search();
  }

  search(): void {
    this.orderratingService.load(this.filter);
  }

  select(selected: Orderrating): void {
    this.selectedOrderrating = selected;
  }

  delete(orderrating: Orderrating): void {
    if (confirm('Are you sure?')) {
      this.orderratingService.delete(orderrating).subscribe(() => {
          this.feedback = {type: 'success', message: 'Delete was successful!'};
          setTimeout(() => {
            this.search();
          }, 1000);
        },
        err => {
          this.feedback = {type: 'warning', message: 'Error deleting.'};
        }
      );
    }
  }

  get ratingCalculatedList(): any{
    console.log('ratingCalculatedList called ..');
    let five = this.orderratingService.ratingCalculatedList['5'] ? this.orderratingService.ratingCalculatedList['5'].length: 0;
    let four =  this.orderratingService.ratingCalculatedList['4'] ? this.orderratingService.ratingCalculatedList['4'].length: 0;
    let three = this.orderratingService.ratingCalculatedList['3'] ? this.orderratingService.ratingCalculatedList['3'].length: 0;
    let two =  this.orderratingService.ratingCalculatedList['2'] ? this.orderratingService.ratingCalculatedList['2'].length: 0;
    let one =  this.orderratingService.ratingCalculatedList['1'] ? this.orderratingService.ratingCalculatedList['1'].length: 0;
    let totalRatings =  (five+four+three+two+one);
    let averageUserRating =  ((5*five) + (4*four) + (3*three) + (2*two) +(1*one) ) / totalRatings;
    this.orderratingService.ratingCalculatedList.averageUserRating = averageUserRating.toFixed(2);
    this.orderratingService.ratingCalculatedList.averageUserRatingRounded = Math.round(averageUserRating);
    this.orderratingService.ratingCalculatedList.totalRatings = totalRatings;

    return this.orderratingService.ratingCalculatedList; //.map(item => {item: item.length});
    

  }
}
