import { Routes } from '@angular/router';
import { ActivationListComponent } from './activation-list/activation-list.component';
import { ActivationEditComponent } from './activation-edit/activation-edit.component';
import {HomeLayoutComponent} from '../layouts/home-layout.component';

export const ACTIVATION_ROUTES: Routes = [
  {
    path: 'home',
    component: HomeLayoutComponent,
    children: [
      {
        path: 'activation',
        component: ActivationListComponent
      }
    ]
  },
  {
    path: 'home',
    component: HomeLayoutComponent,
    children: [
      {
        path: 'activation/:id',
        component: ActivationEditComponent
      }
    ]
  }
]
