import { Customerfeedback } from './customerfeedback';
import { CustomerfeedbackFilter } from './customerfeedback-filter';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';

const headers = new HttpHeaders().set('Accept', 'application/json');

@Injectable()
export class CustomerfeedbackService {
  customerfeedbackList: Customerfeedback[] = [];
  api = '/feedbacks';

  constructor(private http: HttpClient) {
  }

  findById(id: string): Observable<Customerfeedback> {
    const url = `${this.api}/${id}`;
    const params = { id: id };
    return this.http.get<Customerfeedback>(url, {params, headers});
  }

  load(filter: CustomerfeedbackFilter): void {
    this.find(filter).subscribe(result => {
        this.customerfeedbackList = result;
      },
      err => {
        console.error('error loading', err);
      }
    );
  }

  find(filter: CustomerfeedbackFilter): Observable<Customerfeedback[]> {
    const params = {
      'phone': filter.phone,
    };

    return this.http.get<Customerfeedback[]>(this.api, {params, headers});
  }

  save(entity: Customerfeedback): Observable<Customerfeedback> {
    let params = new HttpParams();
    let url = '';
    if (entity.id) {
      url = `${this.api}/${entity.id.toString()}`;
      params = new HttpParams().set('ID', entity.id.toString());
      return this.http.put<Customerfeedback>(url, entity, {headers, params});
    } else {
      url = `${this.api}`;
      return this.http.post<Customerfeedback>(url, entity, {headers, params});
    }
  }

  delete(entity: Customerfeedback): Observable<Customerfeedback> {
    let params = new HttpParams();
    let url = '';
    if (entity.id) {
      url = `${this.api}/${entity.id.toString()}`;
      params = new HttpParams().set('ID', entity.id.toString());
      return this.http.delete<Customerfeedback>(url, {headers, params});
    }
    return null;
  }

   
}

