import { Stock } from './stock';
import { StockFilter } from './stock-filter';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';

const headers = new HttpHeaders().set('Accept', 'application/json');

@Injectable()
export class StockService {
  stockList: Stock[] = [];
  api = '/stocks';

  constructor(private http: HttpClient) {
  }

  findById(id: string): Observable<Stock> {
    const url = `${this.api}/${id}`;
    const params = { id: id };
    return this.http.get<Stock>(url, {params, headers});
  }

  load(filter: StockFilter): void {
    this.find(filter).subscribe(result => {
        this.stockList = result;
      },
      err => {
        console.error('error loading', err);
      }
    );
  }

  find(filter: StockFilter): Observable<Stock[]> {
    const params = {
    };

    return this.http.get<Stock[]>(this.api, {params, headers});
  }

  save(entity: Stock): Observable<Stock> {
    let params = new HttpParams();
    let url = '';
    if (entity.id) {
      url = `${this.api}/${entity.id.toString()}`;
      params = new HttpParams().set('ID', entity.id.toString());
      return this.http.put<Stock>(url, entity, {headers, params});
    } else {
      url = `${this.api}`;
      return this.http.post<Stock>(url, entity, {headers, params});
    }
  }

  delete(entity: Stock): Observable<Stock> {
    let params = new HttpParams();
    let url = '';
    if (entity.id) {
      url = `${this.api}/${entity.id.toString()}`;
      params = new HttpParams().set('ID', entity.id.toString());
      return this.http.delete<Stock>(url, {headers, params});
    }
    return null;
  }
}

