import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Router } from '@angular/router';
import { OrderratingService } from '../orderrating.service';
import { Orderrating } from '../orderrating';
import { map, switchMap } from 'rxjs/operators';
import { of } from 'rxjs';

@Component({
  selector: 'app-orderrating-edit',
  templateUrl: './orderrating-edit.component.html'
})
export class OrderratingEditComponent implements OnInit {

  id: string;
  orderrating: Orderrating;
  feedback: any = {};

  constructor(
    private route: ActivatedRoute,
    private router: Router,
    private orderratingService: OrderratingService) {
  }

  ngOnInit() {
    this
      .route
      .params
      .pipe(
        map(p => p.id),
        switchMap(id => {
          if (id === 'new') { return of(new Orderrating()); }
          return this.orderratingService.findById(id);
        })
      )
      .subscribe(orderrating => {
          this.orderrating = orderrating;
          this.feedback = {};
        },
        err => {
          this.feedback = {type: 'warning', message: 'Error loading'};
        }
      );
  }

  save() {
    this.orderratingService.save(this.orderrating).subscribe(
      orderrating => {
        this.orderrating = orderrating;
        this.feedback = {type: 'success', message: 'Save was successful!'};
        setTimeout(() => {
          this.router.navigate(['/home/orderratings']);
        }, 1000);
      },
      err => {
        this.feedback = {type: 'warning', message: 'Error saving'};
      }
    );
  }

  cancel() {
    this.router.navigate(['/home/orderratings']);
  }
}
