import { Routes } from '@angular/router';
import { CustomerfeedbackListComponent } from './customerfeedback-list/customerfeedback-list.component';
import { CustomerfeedbackEditComponent } from './customerfeedback-edit/customerfeedback-edit.component';
import {HomeLayoutComponent} from '../layouts/home-layout.component';



export const CUSTOMERFEEDBACK_ROUTES: Routes = [
  {
    path: 'home',
    component: HomeLayoutComponent,
    children: [
      {
        path: 'customerfeedbacks',
        component: CustomerfeedbackListComponent
      }
    ]
  },
  {
    path: 'home',
    component: HomeLayoutComponent,
    children: [
      {
        path: 'customerfeedbacks/:id',
        component: CustomerfeedbackEditComponent
      }
    ]
  }
];
