import { Component, OnInit } from '@angular/core';
import { PriceplanFilter } from '../priceplan-filter';
import { PriceplanService } from '../priceplan.service';
import { Priceplan } from '../priceplan';

@Component({
  selector: 'app-priceplan',
  templateUrl: 'priceplan-list.component.html'
})
export class PriceplanListComponent implements OnInit {

  filter = new PriceplanFilter();
  selectedPriceplan: Priceplan;
  feedback: any = {};

  get priceplanList(): Priceplan[] {
    return this.priceplanService.priceplanList;
  }

  constructor(private priceplanService: PriceplanService) {
  }

  ngOnInit() {
    this.search();
  }

  search(): void {
    this.priceplanService.load(this.filter);
  }

  select(selected: Priceplan): void {
    this.selectedPriceplan = selected;
  }

  delete(priceplan: Priceplan): void {
    if (confirm('Are you sure?')) {
      this.priceplanService.delete(priceplan).subscribe(() => {
          this.feedback = {type: 'success', message: 'Delete was successful!'};
          setTimeout(() => {
            this.search();
          }, 1000);
        },
        err => {
          this.feedback = {type: 'warning', message: 'Error deleting.'};
        }
      );
    }
  }
}
