import { Component, OnInit } from '@angular/core';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { RateplanFilter } from '../rateplan-filter';
import { RateplanService } from '../rateplan.service';
import { Rateplan } from '../rateplan';

@Component({
    selector: 'rateplan',
    templateUrl: 'rateplan-list.component.html'
})
export class RateplanListComponent {

    filter = new RateplanFilter();
    selectedRateplan: Rateplan;
    errors = '';

    get rateplanList(): Rateplan[] {
        return this.rateplanService.rateplanList;
    }

    constructor(private rateplanService: RateplanService) {
    }

    ngOnInit() {
        this.search();
    }

    search(): void {
        this.rateplanService.load(this.filter);
    }

    select(selected: Rateplan): void {
        this.selectedRateplan = selected;
    }

    delete(rateplan: Rateplan): void {
        if (confirm("Are you sure?")) {
            this.rateplanService.delete(rateplan).subscribe(
                rateplan => {
                    this.errors = 'Delete was successful!';
                    setTimeout(() => {
                        this.search();
                    },1000);                   
                },
                err => {
                    this.errors = err;
                }
            );
        }        
        
    }    

}
