import { Component, OnInit } from '@angular/core';
import { CustomerfeedbackFilter } from '../customerfeedback-filter';
import { CustomerfeedbackService } from '../customerfeedback.service';
import { Customerfeedback } from '../customerfeedback';

@Component({
  selector: 'app-customerfeedback',
  templateUrl: 'customerfeedback-list.component.html'
})
export class CustomerfeedbackListComponent implements OnInit {

  filter = new CustomerfeedbackFilter();
  selectedCustomerfeedback: Customerfeedback;
  feedback: any = {};

  get customerfeedbackList(): Customerfeedback[] {
    return this.customerfeedbackService.customerfeedbackList;
  }

  constructor(private customerfeedbackService: CustomerfeedbackService) {
  }

  ngOnInit() {
    this.search();
  }

  search(): void {
    this.customerfeedbackService.load(this.filter);
  }

  select(selected: Customerfeedback): void {
    this.selectedCustomerfeedback = selected;
  }

  delete(customerfeedback: Customerfeedback): void {
    if (confirm('Are you sure?')) {
      this.customerfeedbackService.delete(customerfeedback).subscribe(() => {
          this.feedback = {type: 'success', message: 'Delete was successful!'};
          setTimeout(() => {
            this.search();
          }, 1000);
        },
        err => {
          this.feedback = {type: 'warning', message: 'Error deleting.'};
        }
      );
    }
  }

  reviewed (customerfeedback: Customerfeedback): void{
    customerfeedback.isReviewed = true;
    this.customerfeedbackService.save(customerfeedback).subscribe(() => {
      this.feedback = {type: 'success', message: 'update was successful!'};
    },
    err => {
      customerfeedback.isReviewed = false;
      this.feedback = {type: 'warning', message: 'Error deleting.'};
    }
  );
  }
  
}
