import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Router } from '@angular/router';
import { PartnerService } from '../partner.service';
import { Partner } from '../partner';
import { map, switchMap } from 'rxjs/operators';
import { of } from 'rxjs';
import { FileDetector } from 'selenium-webdriver/remote';

@Component({
  selector: 'partner-edit',
  templateUrl: './partner-edit.component.html'
})
export class PartnerEditComponent implements OnInit {

    id: string;
    partner: Partner;
    errors: string;
    partnertypesId_list: any;
    latitude: number;    
    longitude: number;
    

    constructor(
        private route: ActivatedRoute,
        private router: Router,
        private partnerService: PartnerService) { 
    }

    ngOnInit() {
        this
            .route
            .params
            .pipe(
                map(p => p['id']),
                switchMap(id => {
                    if (id === 'new') return of(new Partner());
                    return this.partnerService.findById(id)
                })
            )
            .subscribe(
                partner => { 
                    this.partner = partner; 
                    if(partner.location.coordinates && partner.location.coordinates.length > 0){
                        this.longitude = partner.location.coordinates[1];
                        this.latitude = partner.location.coordinates[0];
                    }
                    this.errors = ''; 
                },
                err => { 
                    this.errors = err; 
                }
            );

            this.partnertypesId_list = [{id: 1, typeName: "Hawker"}];
    }

    save() {
        this.partner.location = {
            type: "Point",
            coordinates : [this.latitude, this.longitude]
        };
        this.partnerService.save(this.partner).subscribe(
            partner => { 
                this.partner = partner; 
                this.errors = 'Save was successful!'; 
                setTimeout(() => {
                    this.router.navigate(['/home/partner']);
                }, 1000);                   
            },
            err => { 
                this.errors = err; 
            }
        );
    }

    cancel() {
        this.router.navigate(['/home/partner']);
    }    
}