import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Router } from '@angular/router';
import { ActivationService } from '../activation.service';
import { Activation } from '../activation';
import { map, switchMap } from 'rxjs/operators';
import { of } from 'rxjs';
import { FileDetector } from 'selenium-webdriver/remote';

@Component({
  selector: 'activation-edit',
  templateUrl: './activation-edit.component.html'
})
export class ActivationEditComponent implements OnInit {

    id: string;
    activation: Activation;
    errors: string;
    
    

    constructor(
        private route: ActivatedRoute,
        private router: Router,
        private activationService: ActivationService) { 
    }

    ngOnInit() {
        this
            .route
            .params
            .pipe(
                map(p => p['id']),
                switchMap(id => {
                    if (id === 'new') return of(new Activation());
                    return this.activationService.findById(id)
                })
            )
            .subscribe(
                activation => { 
                    this.activation = activation; 
                    this.errors = ''; 
                },
                err => { 
                    this.errors = err; 
                }
            );
    }

    save() {
        this.activationService.save(this.activation).subscribe(
            activation => { 
                this.activation = activation; 
                this.errors = 'Save was successful!'; 
                setTimeout(() => {
                    this.router.navigate(['/home/activation']);
                }, 1000);                   
            },
            err => { 
                this.errors = err; 
            }
        );
    }

    cancel() {
        this.router.navigate(['/home/activation']);
    }    
}