import { Activation } from './activation';
import { ActivationFilter } from './activation-filter';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
//import {HttpHeaderService} from '../services/index'

@Injectable()
export class ActivationService {
    
    baseUrl = `/activations`;
    constructor(private http: HttpClient) {
    }

    activationList: Activation[] = [];
  
    findById(id: string): Observable<Activation> {
        
        let url = `${this.baseUrl}/${id}`; 
        let params = { "id": id };
         let headers = new HttpHeaders()
                             .set('Accept', 'application/json')
                                    
        return this.http.get<Activation>(url, {params, headers});
    }
    
    load(filter: ActivationFilter): void {
        
        this.find(filter).subscribe(
            result => {
                this.activationList = result;
            },
            err => {
                console.error('error loading', err);
            }
        )
    }

    find(filter: ActivationFilter): Observable<Activation[]> {
        let url = `${this.baseUrl}`;      
        let headers = new HttpHeaders()
                            .set('Accept', 'application/json');

        let params = {
            "phone": filter.phone,
        };

        return this.http.get<Activation[]>(url, {params, headers});
    }

    save(entity: Activation): Observable<Activation> {        
        var params = new HttpParams();
        var url = "";
        const headers = new HttpHeaders().set('content-type', 'application/json');  
        if(entity.id) {
            url = `${this.baseUrl}/${entity.id.toString()}`;
            params = new HttpParams().set('ID', entity.id.toString());  
            return this.http.put<Activation>(url, entity, {headers, params});
        }
        else {
            url = `${this.baseUrl}`;
            return this.http.post<Activation>(url, entity, {headers, params});
        }        
    }


    delete(entity: Activation): Observable<Activation> {
        var params = new HttpParams();
        var url = "";
        const headers = new HttpHeaders().set('content-type', 'application/json');  
        if(entity.id) {
            url = `${this.baseUrl}/${entity.id.toString()}`;
            params = new HttpParams().set('ID', entity.id.toString());  
            return this.http.delete<Activation>(url, {headers, params});
        }

        return null;
    }       
}

