import { Component, OnInit } from '@angular/core';
import { ConfigurationFilter } from '../configuration-filter';
import { ConfigurationService } from '../configuration.service';
import { Configuration } from '../configuration';

@Component({
  selector: 'app-configuration',
  templateUrl: 'configuration-list.component.html'
})
export class ConfigurationListComponent implements OnInit {

  filter = new ConfigurationFilter();
  selectedConfiguration: Configuration;
  feedback: any = {};

  get configurationList(): Configuration[] {
    return this.configurationService.configurationList;
  }

  constructor(private configurationService: ConfigurationService) {
  }

  ngOnInit() {
    this.search();
  }

  search(): void {
    this.configurationService.load(this.filter);
  }

  select(selected: Configuration): void {
    this.selectedConfiguration = selected;
  }

  delete(configuration: Configuration): void {
    if (confirm('Are you sure?')) {
      this.configurationService.delete(configuration).subscribe(() => {
          this.feedback = {type: 'success', message: 'Delete was successful!'};
          setTimeout(() => {
            this.search();
          }, 1000);
        },
        err => {
          this.feedback = {type: 'warning', message: 'Error deleting.'};
        }
      );
    }
  }
}
