import { Component, OnInit } from '@angular/core';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { CustomerFilter } from '../customer-filter';
import { CustomerService } from '../customer.service';
import { Customer } from '../customer';

@Component({
    selector: 'customer',
    templateUrl: 'customer-list.component.html'
})
export class CustomerListComponent {

    filter = new CustomerFilter();
    selectedCustomer: Customer;
    errors = '';
        
    //config: any;
    

    get customerList(): Customer[] {
        return this.customerService.customerList;
    }

    constructor(private customerService: CustomerService) {
        // this.config = {
        //     itemsPerPage: 5,
        //     currentPage: 1,
        //    // totalItems: this.collection.count
        //   };
    }

    ngOnInit() {
        //this.search();
    }

    // pageChanged(event){
    //     this.config.currentPage = event;
    //   }

    search(): void {
        this.customerService.load(this.filter);
    }

    select(selected: Customer): void {
        this.selectedCustomer = selected;
    }

    delete(customer: Customer): void {
        if (confirm("Are you sure?")) {
            this.customerService.delete(customer).subscribe(
                customer => {
                    this.errors = 'Delete was successful!';
                    setTimeout(() => {
                        this.search();
                    },1000);                   
                },
                err => {
                    this.errors = err;
                }
            );
        }        
        
    }    

}
