import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Router } from '@angular/router';
import { CustomerfeedbackService } from '../customerfeedback.service';
import { Customerfeedback } from '../customerfeedback';
import { map, switchMap } from 'rxjs/operators';
import { of } from 'rxjs';

@Component({
  selector: 'app-customerfeedback-edit',
  templateUrl: './customerfeedback-edit.component.html'
})
export class CustomerfeedbackEditComponent implements OnInit {

  id: string;
  customerfeedback: Customerfeedback;
  feedback: any = {};

  constructor(
    private route: ActivatedRoute,
    private router: Router,
    private customerfeedbackService: CustomerfeedbackService) {
  }

  ngOnInit() {
    this
      .route
      .params
      .pipe(
        map(p => p.id),
        switchMap(id => {
          if (id === 'new') { return of(new Customerfeedback()); }
          return this.customerfeedbackService.findById(id);
        })
      )
      .subscribe(customerfeedback => {
          this.customerfeedback = customerfeedback;
          this.feedback = {};
        },
        err => {
          this.feedback = {type: 'warning', message: 'Error loading'};
        }
      );
  }

  save() {
    this.customerfeedbackService.save(this.customerfeedback).subscribe(
      customerfeedback => {
        this.customerfeedback = customerfeedback;
        this.feedback = {type: 'success', message: 'Save was successful!'};
        setTimeout(() => {
          this.router.navigate(['/home/customerfeedbacks']);
        }, 1000);
      },
      err => {
        this.feedback = {type: 'warning', message: 'Error saving'};
      }
    );
  }

  cancel() {
    this.router.navigate(['/home/customerfeedbacks']);
  }
}
