import { Supply } from './supply';
import { SupplyFilter } from './supply-filter';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';

@Injectable()
export class SupplyService {
    
    baseUrl = `/supplies`;
    constructor(private http: HttpClient) {
    }

    supplyList: Supply[] = [];
  
    findById(id: string): Observable<Supply> {
        let url = `${this.baseUrl}/${id}`; 
        let params = { "id": id };
        let headers = new HttpHeaders()
                            .set('Accept', 'application/json');
        return this.http.get<Supply>(url, {params, headers});
    }
    
    load(filter: SupplyFilter): void {
        this.find(filter).subscribe(
            result => {
                this.supplyList = result;
            },
            err => {
                console.error('error loading', err);
            }
        )
    }

    find(filter: SupplyFilter): Observable<Supply[]> {
        let url = `${this.baseUrl}`;
        let headers = new HttpHeaders()
                            .set('Accept', 'application/json');

        let params = {
            "referenceNo": filter.referenceNo,
        };

        return this.http.get<Supply[]>(url, {params, headers});
    }

    save(entity: Supply): Observable<Supply> {        
        var params = new HttpParams();
        var url = "";
        const headers = new HttpHeaders().set('content-type', 'application/json');  
        if(entity.id) {
            url = `${this.baseUrl}/${entity.id.toString()}`;
            params = new HttpParams().set('ID', entity.id.toString());  
            return this.http.put<Supply>(url, entity, {headers, params});
        }
        else {
            url = `${this.baseUrl}`;
            return this.http.post<Supply>(url, entity, {headers, params});
        }        
    }


    delete(entity: Supply): Observable<Supply> {
        var params = new HttpParams();
        var url = "";
        const headers = new HttpHeaders().set('content-type', 'application/json');  
        if(entity.id) {
            url = `${this.baseUrl}/${entity.id.toString()}`;
            params = new HttpParams().set('ID', entity.id.toString());  
            return this.http.delete<Supply>(url, {headers, params});
        }

        return null;
    }
    
    supplyArrived(supplyId: string): Observable<Supply> {        
        var params = new HttpParams();
        var url = "";
        const headers = new HttpHeaders().set('content-type', 'application/json');  
       
            url = `${this.baseUrl}/${supplyId}/arrived`;
            return this.http.patch<Supply>(url, {}, {headers, params});
       
    }


}

