import { Component, OnInit } from '@angular/core';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { SupplyFilter } from '../supply-filter';
import { SupplyService } from '../supply.service';
import { Supply } from '../supply';

@Component({
    selector: 'supply',
    templateUrl: 'supply-list.component.html'
})
export class SupplyListComponent {

    filter = new SupplyFilter();
    selectedSupply: Supply;
    errors = '';

    get supplyList(): Supply[] {
        return this.supplyService.supplyList;
    }

    constructor(private supplyService: SupplyService) {
    }

    ngOnInit() {
        this.search();
    }

    search(): void {
        this.supplyService.load(this.filter);
    }

    select(selected: Supply): void {
        this.selectedSupply = selected;
    }

    delete(supply: Supply): void {
        if (confirm("Are you sure?")) {
            this.supplyService.delete(supply).subscribe(
                supply => {
                    this.errors = 'Delete was successful!';
                    setTimeout(() => {
                        this.search();
                    },1000);                   
                },
                err => {
                    this.errors = 'Error deleting.';
                }
            );
        }                
    }    

}
