import { Orders } from './orders';
import { OrdersFilter } from './orders-filter';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';

const headers = new HttpHeaders().set('Accept', 'application/json');

@Injectable()
export class OrdersService {
  ordersList: Orders[] = [];
  api = 'http://192.168.10.9:3000/orders';

  constructor(private http: HttpClient) {
  }

  findById(id: string): Observable<Orders> {
    const url = `${this.api}/${id}`;
    const params = { id: id };
    return this.http.get<Orders>(url, {params, headers});
  }

  load(filter: OrdersFilter): void {
    this.find(filter).subscribe(result => {
        this.ordersList = result;
      },
      err => {
        console.error('error loading', err);
      }
    );
  }

  find(filter: OrdersFilter): Observable<Orders[]> {
    let url = `${this.api}`
    const params = {
      
    };
    if(filter.orderStatus || filter.isOrderRatingDone || filter.id ){
      url = `${this.api}?filter[where]`
    }
    if(filter.orderStatus){
      url = url + `[orderStatus]=${filter.orderStatus}`
    }
    
    if(filter.isOrderRatingDone){
      url = url + `[isOrderRatingDone]=${filter.isOrderRatingDone}`
    }
    if(filter.id){
      url = url + `[id]=${filter.id}`
    }
    return this.http.get<Orders[]>(url, {params, headers});
  }

  save(entity: Orders): Observable<Orders> {
    let params = new HttpParams();
    let url = '';
    if (entity.id) {
      url = `${this.api}/${entity.id.toString()}`;
      params = new HttpParams().set('ID', entity.id.toString());
      return this.http.put<Orders>(url, entity, {headers, params});
    } else {
      url = `${this.api}`;
      return this.http.post<Orders>(url, entity, {headers, params});
    }
  }

  delete(entity: Orders): Observable<Orders> {
    let params = new HttpParams();
    let url = '';
    const currentUser = JSON.parse(localStorage.getItem('currentUser'));
    if (entity.id) {
      url = `${this.api}/${entity.id.toString()}/users/${currentUser.id}`;
      params = new HttpParams().set('ID', entity.id.toString());
      return this.http.delete<Orders>(url, {headers, params});
    }
    return null;
  }

  orderStartProgress(entity: Orders): Observable<Orders> {
    let params = new HttpParams();
    let url = '';
   
      url = `${this.api}/${entity.id.toString()}/customers/${entity.customersId}/startProgress`;
     // params = new HttpParams().set('ID', entity.id.toString());
      return this.http.patch<Orders>(url, entity, {headers, params});
   
  }

  orderDelivered(entity: Orders): Observable<Orders> {
    let params = new HttpParams();
    let url = '';
      url = `${this.api}/${entity.id.toString()}/customers/${entity.customersId.toString()}/delevered`;
      //params = new HttpParams().set('ID', entity.id.toString());
      return this.http.patch<Orders>(url,  {headers, params});
  
  }

  cancelOrder(entity: Orders): Observable<Orders> {
    const currentUser = JSON.parse(localStorage.getItem('currentUser'));
    let userId = currentUser.id;
    let params = new HttpParams();
    let url = '';
      url = `${this.api}/${entity.id.toString()}/users/${userId}/cancel`;
      //params = new HttpParams().set('ID', entity.id.toString());
      return this.http.patch<Orders>(url,  {headers, params});
  
  }

  updateQuantity(orderId: String, productId: String, quantity: number): Observable<Orders> {
    const currentUser = JSON.parse(localStorage.getItem('currentUser'));
    let userId = currentUser.id;
    let params = new HttpParams();
    let url = '';
      url = `${this.api}/${orderId.toString()}/products/${productId}/${quantity}`;
      //params = new HttpParams().set('ID', entity.id.toString());
      return this.http.patch<Orders>(url,  {headers, params});
  
  }

}

