import { Routes } from '@angular/router';
import { PriceplanListComponent } from './priceplan-list/priceplan-list.component';
import { PriceplanEditComponent } from './priceplan-edit/priceplan-edit.component';
import {HomeLayoutComponent} from '../layouts/home-layout.component';


export const PRICEPLAN_ROUTES: Routes = [
  {
    path: 'home',
    component: HomeLayoutComponent,
    children: [
      {
        path: 'priceplans',
        component: PriceplanListComponent
      }
    ]
  },
  {
    path: 'home',
    component: HomeLayoutComponent,
    children: [
      {
        path: 'priceplans/:id',
        component: PriceplanEditComponent
      }
    ]
  }
];
