import { Routes } from '@angular/router';
import { OrderratingListComponent } from './orderrating-list/orderrating-list.component';
import { OrderratingEditComponent } from './orderrating-edit/orderrating-edit.component';
import {HomeLayoutComponent} from '../layouts/home-layout.component';


export const ORDERRATING_ROUTES: Routes = [
  {
    path: 'home',
    component: HomeLayoutComponent,
    children: [
      {
        path: 'orderratings',
        component: OrderratingListComponent
      }
    ]
  },
  {
    path: 'home',
    component: HomeLayoutComponent,
    children: [
      {
        path: 'orderratings/:id',
        component: OrderratingEditComponent
      }
    ]
  }
];
