import { Component, OnInit } from '@angular/core';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { ActivationFilter } from '../activation-filter';
import { ActivationService } from '../activation.service';
import { Activation } from '../activation';


@Component({
    selector: 'activation',
    templateUrl: 'activation-list.component.html'
})
export class ActivationListComponent {

    filter = new ActivationFilter();
    selectedActivation: Activation;
    errors = '';

    get activationList(): Activation[] {
        return this.activationService.activationList;
    }

    constructor(private activationService: ActivationService,
        
        ) {
        
    }

    ngOnInit() {
        this.search();
    }

    search(): void {
        this.activationService.load(this.filter);
    }

    select(selected: Activation): void {
        this.selectedActivation = selected;
    }

    delete(activation: Activation): void {
        if (confirm("Are you sure?")) {
            this.activationService.delete(activation).subscribe(
                activation => {
                    this.errors = 'Delete was successful!';
                    setTimeout(() => {
                        this.search();
                    },1000);                   
                },
                err => {
                    this.errors = err;
                }
            );
        }        
        
    }    

}
