import { Orderrating } from './orderrating';
import { OrderratingFilter } from './orderrating-filter';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';

const headers = new HttpHeaders().set('Accept', 'application/json');

@Injectable()
export class OrderratingService {
  orderratingList: Orderrating[] = [];
  ratingCalculatedList: any;
  api = '/order-ratings';

  constructor(private http: HttpClient) {
  }

  findById(id: string): Observable<Orderrating> {
    const url = `${this.api}/${id}`;
    const params = { id: id };
    return this.http.get<Orderrating>(url, {params, headers});
  }

  load(filter: OrderratingFilter): void {
    this.find(filter).subscribe(result => {
        this.orderratingList = result;
        this.ratingCalculatedList = this.calculateRatingAverage();
        console.log("this.ratingCalculatedList", this.ratingCalculatedList)
      },
      err => {
        console.error('error loading', err);
      }
    );
  }

  find(filter: OrderratingFilter): Observable<Orderrating[]> {
    const params = {
    };

    return this.http.get<Orderrating[]>(this.api, {params, headers});
  }

  save(entity: Orderrating): Observable<Orderrating> {
    let params = new HttpParams();
    let url = '';
    if (entity.id) {
      url = `${this.api}/${entity.id.toString()}`;
      params = new HttpParams().set('ID', entity.id.toString());
      return this.http.put<Orderrating>(url, entity, {headers, params});
    } else {
      url = `${this.api}`;
      return this.http.post<Orderrating>(url, entity, {headers, params});
    }
  }

  delete(entity: Orderrating): Observable<Orderrating> {
    let params = new HttpParams();
    let url = '';
    if (entity.id) {
      url = `${this.api}/${entity.id.toString()}`;
      params = new HttpParams().set('ID', entity.id.toString());
      return this.http.delete<Orderrating>(url, {headers, params});
    }
    return null;
  }

  calculateRatingAverage() :any {
    return this.orderratingList.reduce((r, a) => {
      console.log("a", a);
     // console.log('r', r);
     //r[a.points] = r[a.points] ? r[a.points] +1 : 1; //[...r[a.points] || [], a];
     r[a.points] = [...r[a.points] || [], a];
      return r;
     }, {});
  }

}

