import { Component, OnInit, HostListener } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Router } from '@angular/router';
import { OrdersService } from '../orders.service';
import { Orders } from '../orders';
import { map, switchMap } from 'rxjs/operators';
import { of } from 'rxjs';


@Component({
  selector: 'app-orders-edit',
  templateUrl: './orders-edit.component.html'
})
export class OrdersEditComponent implements OnInit {


  id: string;
  orders: Orders;
  feedback: any = {};
  locationUrl: string = 'https://www.google.com/maps/dir'
  isPrinting: boolean = false;


  constructor(
    private route: ActivatedRoute,
    private router: Router,
    private ordersService: OrdersService,

    ) {

  }

  ngOnInit() {
    this
      .route
      .params
      .pipe(
        map(p => p.id),
        switchMap(id => {
          if (id === 'new') { return of(new Orders()); }
          return this.ordersService.findById(id);
        })
      )
      .subscribe(orders => {
          this.orders = orders;
          this.feedback = {};
          if(this.orders.location && this.orders.location.coordinates){
            this.locationUrl = `${this.locationUrl}/${this.orders.location.coordinates[1]},${this.orders.location.coordinates[0]}`
          }
        },
        err => {
          this.feedback = {type: 'warning', message: 'Error loading'};
        }
      );
  }


  save() {
    this.ordersService.save(this.orders).subscribe(
      orders => {
        this.orders = orders;
        this.feedback = {type: 'success', message: 'Save was successful!'};
        setTimeout(() => {
          this.router.navigate(['/home/orderses']);
        }, 1000);
      },
      err => {
        this.feedback = {type: 'warning', message: 'Error saving'};
      }
    );
  }

  cancel() {
    this.router.navigate(['/home/orderses']);
  }

  orderStartProgress(){
    this.ordersService.orderStartProgress(this.orders).subscribe(
      resp => {
       // this.orders = orders;
        this.feedback = {type: 'success', message: 'Progress started!'};
        this.orders.orderStatus = resp.orderStatus
      },
      err => {
        this.feedback = {type: 'warning', message: 'Error saving'};
      }
    );
  }
  orderDelivered(){
    this.ordersService.orderDelivered(this.orders).subscribe(
      resp => {
        //this.orders = orders;
        this.feedback = {type: 'success', message: 'Delivered successful!'};
        this.orders.orderStatus = resp.orderStatus;
      },
      err => {
        this.feedback = {type: 'warning', message: 'Error saving'};
      }
    );
  }

  cancelOrder(){
    if (confirm('Are you sure to Cancel?')) {
        this.ordersService.cancelOrder(this.orders).subscribe(
          resp => {
            //this.orders = orders;
            this.feedback = {type: 'success', message: 'Canel successful!'};
            this.orders.orderStatus = resp.orderStatus;
          },
          err => {
            this.feedback = {type: 'warning', message: 'Error saving'};
          }
        );
      }
    }

    updateQuantity(productId, quantity){
      if (confirm('Are you want to update?')) {
        this.ordersService.updateQuantity(this.orders.id, productId, quantity).subscribe(
          resp => {
            //this.orders = orders;
            
            this.feedback = {type: 'success', message: 'Updated successful!'};
            this.totalBill();
            //this.orders.orderStatus = resp.orderStatus;
          },
          err => {
            this.feedback = {type: 'warning', message: 'Error saving'};
          }
        );
      }
    }

    totalBill(){
      this.orders.items.forEach((element, index) => {
        if(index === 0){
          this.orders.totalBillAmount = element.quantity * element.price

        }else{
          this.orders.totalBillAmount = this.orders.totalBillAmount + (element.quantity * element.price);
        }
        
      });
    }

  printReceipt(){
    this.isPrinting = true;
    setTimeout(function() {
      var html="<html>";
      html += '<h1>kellostore</h1>';
      html += '<p>Store No: G11 Markaz, Algafar Plaza Basement Shop No: 08</p>';
      html += '<p>Phone: 923015339780</p>';
      html += '<p>Order Id: ';
      html += document.getElementById("orderId").nextSibling.textContent;
      html += '</p>';
  
      html += '<p>Date: ';
      html += document.getElementById("orderTime").nextSibling.textContent;
      html += '</p>';
  
      html += '<p>Customer: ';
      html += document.getElementById("customerName").nextSibling.textContent;
      html += '</p>';
  
      html += '<hr />';
  
      html += '<p>Detail: </p>';
      html += document.getElementsByClassName('table-responsive')[0].innerHTML;
  
      html += '<hr />';
  
      html += '<p>Total bill: ';
      html += document.getElementById("totalBillAmount").nextSibling.textContent;
      html += '</p>';
  
      html += '<hr />';
  
      html+="</html>";
  
        var printWin = window.open('','','left=50,top=10,width=100,height=100,toolbar=0,scrollbars=0,status  =0');
        printWin.document.write(html);
        printWin.document.close();
        printWin.focus();
        printWin.print();
        printWin.close();

        
  
    }, 100);
   
    
  }


  @HostListener('window:focus', ['$event'])
   onFocus(event: FocusEvent): void {
    this.isPrinting = false;
       // Do something      

   }

   @HostListener('window:blur', ['$event'])
   onBlur(event: FocusEvent): void {
    this.isPrinting = false;
      // Do something

   }

    
}
