import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Router } from '@angular/router';
import { StockService } from '../stock.service';
import { Stock } from '../stock';
import { Product } from '../../product/product';
import { ProductService } from '../../product/product.service';
import { ProductFilter } from '../../product/product-filter';

import { map, switchMap } from 'rxjs/operators';
import { of } from 'rxjs';

@Component({
  selector: 'app-stock-edit',
  templateUrl: './stock-edit.component.html'
})
export class StockEditComponent implements OnInit {

  id: string;
  stock: Stock;
  feedback: any = {};

  productFilter = new ProductFilter();
 
    get productsId_list(): Product[] {
        return this.productService.productList;
    }

  constructor(
    private route: ActivatedRoute,
    private router: Router,
    private stockService: StockService,
    private productService: ProductService,
    ) {
      
  }

  ngOnInit() {
    this
      .route
      .params
      .pipe(
        map(p => p.id),
        switchMap(id => {
          if (id === 'new') { return of(new Stock()); }
          return this.stockService.findById(id);
        })
      )
      .subscribe(stock => {
          this.stock = stock;
          this.feedback = {};
        },
        err => {
          this.feedback = {type: 'warning', message: 'Error loading'};
        }
      );

      this.productService.load(this.productFilter);
  }

  selectedProduct($event) {
    let seletectProduct = this.productsId_list.filter( p => p.id == this.stock.productsId);
    console.log(seletectProduct);
    if(seletectProduct.length > 0){
        this.stock.productTitle = seletectProduct[0].displayName;
    }
}

  save() {
    this.stockService.save(this.stock).subscribe(
      stock => {
        this.stock = stock;
        this.feedback = {type: 'success', message: 'Save was successful!'};
        setTimeout(() => {
          this.router.navigate(['/home/stocks']);
        }, 1000);
      },
      err => {
        this.feedback = {type: 'warning', message: 'Error saving'};
      }
    );
  }

  cancel() {
    this.router.navigate(['/home/stocks']);
  }
}
