import { Product } from './product';
import { ProductFilter } from './product-filter';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';

@Injectable()
export class ProductService {
    
    baseUrl = `/products`;
    constructor(private http: HttpClient) {
    }

    productList: Product[] = [];
  
    findById(id: string): Observable<Product> {
        let url = `${this.baseUrl}/${id}`; 
        let params = { "id": id };
        let headers = new HttpHeaders()
                            .set('Accept', 'application/json');
        return this.http.get<Product>(url, {params, headers});
    }
    
    load(filter: ProductFilter): void {
        this.find(filter).subscribe(
            result => {
                this.productList = result;
            },
            err => {
                console.error('error loading', err);
            }
        )
    }

    find(filter: ProductFilter): Observable<Product[]> {
        let url = `${this.baseUrl}`;
        if(filter.eng_title || filter.isAvailable ){
            url = `${this.baseUrl}?filter[where]`
        }
        if(filter.eng_title){
            url = url + `[eng_title]=${filter.eng_title}`
        }
        if(filter.isAvailable){
            url = url + `[isAvailable]=${filter.isAvailable}`
        }
        let headers = new HttpHeaders()
                            .set('Accept', 'application/json');

        let params = {
            "eng_title": filter.eng_title,
        };

        return this.http.get<Product[]>(url, {params, headers});
    }

    save(entity: Product): Observable<Product> {        
        var params = new HttpParams();
        var url = "";
        entity.image ? 0: entity.image = '';
        entity.quentityUnitsId ? 0: entity.quentityUnitsId = '';
        const headers = new HttpHeaders().set('content-type', 'application/json');  
        if(entity.id) {
            url = `${this.baseUrl}/${entity.id.toString()}`;
            params = new HttpParams().set('ID', entity.id.toString());  
            return this.http.put<Product>(url, entity, {headers, params});
        }
        else {
            url = `${this.baseUrl}`;
            return this.http.post<Product>(url, entity, {headers, params});
        }        
    }


    delete(entity: Product): Observable<Product> {
        var params = new HttpParams();
        var url = "";
        const headers = new HttpHeaders().set('content-type', 'application/json');  
        if(entity.id) {
            url = `${this.baseUrl}/${entity.id.toString()}`;
            params = new HttpParams().set('ID', entity.id.toString());  
            return this.http.delete<Product>(url, {headers, params});
        }

        return null;
    }       
}

